<template>
  <el-card class="box-card">
    <el-form ref="dataForm" :model="param" :rules="rule" label-width="80px">
      <el-form-item label="owner" prop="owner">
        <el-input v-model="param.owner" placeholder="owner"></el-input>
      </el-form-item>
      <el-form-item label="longName" prop="longName">
        <el-input v-model="param.longName" placeholder="longName"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">confirm</el-button>
      </el-form-item>
    </el-form>
  </el-card>
</template>

<script>
export default {
  data() {
    return {
      param: {
        longName: '',
        owner: '',
      },
      rule: {
        longName: [{required: true, message: "please fill in longName", trigger: 'blur'}],
        owner: [{required: true, message: "please fill in owner", trigger: 'blur'}],
      }
    }
  },
  methods: {
    onSubmit() {
      this.$refs.dataForm.validate((valid) => {
        if (valid) {
          this.$axios.post('/restaurants/start-funding', this.param).then(res => {
            if (res.success) {
              this.$message({
                message: 'operate successfully',
                type: 'success'
              });
              this.$router.back();
            } else {
              this.$message.error(res.message);
            }
          })
        }
      });
    }
  }
}
</script>

